import React from 'react';
import 'lazysizes';

import './Header.css';

import headerImg from './pen_paper.jpg';
import profileImg from './profile_big.jpeg';

function Header() {
    return (
        <div className="HeaderContainer">
            <img
                alt=""
                className="header-bg-img lazyload"
                src={headerImg.placeholder}
                data-sizes="auto"
                data-srcset={headerImg.srcSet}
            />
            <div className="profile-container">
                <img
                    alt=""
                    className="profile-img lazyload"
                    src={profileImg.placeholder}
                    data-sizes="auto"
                    data-srcset={profileImg.srcSet}
                />
            </div>
        </div>
    );
}

export default Header;
