import React from "react"

import Button from "react-bootstrap/Button"

export function SlideButton(props) {
	return <Button variant="primary">{props.text}</Button>
}

export function Slide({ children }) {
	return <div className="StyleSlide text-center">{children}</div>
}

export const settings = {
	dots: true,
	infinite: true,
	speed: 500,
	slidesToShow: 1,
	slidesToScroll: 1,
}
