function Caption(props) {
	if (!Array.isArray(props.caption)) {
		return props.caption
	}

	const items = props.caption.flatMap((value, index, array) =>
		array.length - 1 !== index // check for the last item
			? [value, <br />]
			: value
	)

	return (
		<React.Fragment>
			{items.map((currentValue, index) => (
				<React.Fragment key={index}>{currentValue}</React.Fragment>
			))}
		</React.Fragment>
	)
}

function PortfolioItem(props) {
	return (
		<Card>
			<Card.Header>
				<Accordion.Toggle
					as={Button}
					variant="link"
					eventKey={props.eventKey}
					className="w-100"
				>
					<Caption caption={props.caption} />
				</Accordion.Toggle>
			</Card.Header>
			<Accordion.Collapse eventKey={props.eventKey}>
				<Card.Body>{props.body}</Card.Body>
			</Accordion.Collapse>
		</Card>
	)
}

