import React from 'react';
import { Link, withRouter } from "react-router-dom";

import Nav from 'react-bootstrap/Nav'


function NavLink({to, location, children}) {
    return (
        <Nav.Item>
            <Nav.Link
                as={Link}
                to={to}
                disabled={to === location.pathname}
            >
                {children}
            </Nav.Link>
        </Nav.Item>
    );
}

const NavItem = withRouter(NavLink);

function Navigation() {
    return (
        <Nav className="Navigation">
            <NavItem to="/">
                Резюме
            </NavItem>
            <NavItem to="/portfolio">
                Портфолио
            </NavItem>
        </Nav>
    );
}

export default Navigation;
